import axios from 'axios';

const translations = {
  esToEn: {
    'Fecha': 'Date',
    'Nombre del Proyecto': 'Project Name',
    'Nombre de la Parte Lesionada': 'Injured Party Name',
    'Nombre Completo del Testigo': 'Eyewitness Full Name',
    'Dirección': 'Address',
    'Número de Teléfono': 'Phone Number',
    'Nombre de la Empresa': 'Company Name',
    'Posición': 'Position',
    'Accidente': 'Accident',
    'Lesión': 'Injury',
    'Daño a la Propiedad': 'Property Damage',
    'Fecha del Incidente': 'Date of Incident',
    'Hora del Incidente': 'Time of Incident',
    'Condiciones': 'Conditions',
    'Descripción de la causa y eventos de muestra.': 'Cause and Events',
    'Firma del Testigo': 'Eyewitness Signature',
    'Firma del Representante de Webber Waterworks': 'Webber Waterworks Representative Signature'
  },
  enToEs: {
    'Date': 'Fecha',
    'Project Name': 'Nombre del Proyecto',
    'Injured Party Name': 'Nombre de la Parte Lesionada',
    'Eyewitness Full Name': 'Nombre Completo del Testigo',
    'Address': 'Dirección',
    'Phone Number': 'Número de Teléfono',
    'Company Name': 'Nombre de la Empresa',
    'Position': 'Posición',
    'Accident': 'Accidente',
    'Injury': 'Lesión',
    'Property Damage': 'Daño a la Propiedad',
    'Date of Incident': 'Fecha del Incidente',
    'Time of Incident': 'Hora del Incidente',
    'Conditions': 'Condiciones',
    'Cause and Events': 'Descripción de la causa y eventos de muestra.',
    'Eyewitness Signature': 'Firma del Testigo',
    'Webber Waterworks Representative Signature': 'Firma del Representante de Webber Waterworks'
  }
};

export const translate = (data, direction) => {
  const translationMap = translations[direction];
  const translatedData = { ...data };

  for (const key in data) {
    if (typeof data[key] === 'string' && translationMap[data[key]]) {
      translatedData[key] = translationMap[data[key]];
    } else if (typeof data[key] === 'object' && data[key] !== null) {
      // Handle nested objects like report.span
      translatedData[key] = { ...data[key] };
      for (const nestedKey in data[key]) {
        if (translationMap[data[key][nestedKey]]) {
          translatedData[key][nestedKey] = translationMap[data[key][nestedKey]];
        }
      }
    }
  }

  return translatedData;
};

export const fetchTranslation = async (text, sourceLang, targetLang) => {
  try {
    const response = await axios.get('https://api.mymemory.translated.net/get', {
      params: {
        q: text,
        langpair: `${sourceLang}|${targetLang}`
      }
    });
    return response.data.responseData.translatedText;
  } catch (error) {
    console.error('Error fetching translation:', error);
    return text; // Fallback to the original text if the API fails
  }
};
